import { profession, typesWork, materials, status } from "./base";
import renderCheckboxFilters from "./renderCheckboxFilters";
const checkbox = () => {
  const form = document.querySelector(".form");
  if (form) {
    renderCheckboxFilters(".types", typesWork, "types");
    renderCheckboxFilters(".profession", profession, "profession");
    renderCheckboxFilters(".materials", materials, "materials");
    const checkboxMaterials = document
      .querySelector(".materials")
      .querySelectorAll("label");
    checkboxMaterials.forEach((item) => {
      item.addEventListener("click", () => {
        if (item.querySelector("input").checked) {
          removeCheck(checkboxMaterials);
        } else {
          removeCheck(checkboxMaterials);

          item.querySelector("input").checked = true;
        }
      });
    });
  }
};
function renderCheckbox(selector) {
  const formWrapper = document.querySelector(selector);
  if (formWrapper) {
    const checkboxWrapper = formWrapper.querySelectorAll(".form__checkbox");
    checkboxWrapper.forEach((items) => {
      const checkbox = items.querySelectorAll("input[type=checkbox]");
      checkbox.forEach((item) => {
        const span = document.createElement("span");
        span.classList.add("check");
        span.innerHTML = `<svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill="#F9E14B"><path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z"/></svg>`;
        item.insertAdjacentElement("afterend", span);
      });
    });
  }
}

function renderCheckboxName(selector) {
  const formWrapper = document.querySelector(selector);
  if (formWrapper) {
    const listItem = formWrapper.querySelectorAll(".wpcf7-list-item");
    listItem.forEach((item) => {
      const content = item.innerHTML;
      item.innerHTML = `<label class="d-flex justify-content-center align-items-center">${content}</label>`;
    });
  }
}

function removeCheck(arr) {
  let isRemove;
  arr.forEach((item) => {
    item.querySelector("input").checked = false;
  });
  return isRemove;
}

export default checkbox;
