export default function createDonate(btn) {
  const payData = {
    merchantAccount: "www_dobrobat_in_ua5",
    merchantDomainName: "www.dobrobat.in.ua",
    orderReference: `OBJ-${btn.dataset.id}-${Date.parse(new Date())}`,
    orderDate: Date.parse(new Date()),
    amount: 0,
    merchantSignature: "",
    currency: btn.dataset.currency,
    productName: !isEn
      ? [`Благодійна пожертва на об'єкт - ${btn.dataset.id}`]
      : [`Donate for object - ${btn.dataset.id}`],
    productPrice: [0],
    productCount: [1],
    language: btn.dataset.lang,
    returnUrl: btn.dataset.label
  };
  let {
    merchantAccount,
    merchantDomainName,
    orderReference,
    merchantSignature,
    orderDate,
    amount,
    currency,
    productName = productName[0],
    productCount = productCount[0],
    productPrice = productPrice[0],
    language,
    returnUrl
  } = payData;
  let string = `${merchantAccount};${merchantDomainName};${orderReference};${orderDate};${amount};${currency};${productName};${productCount};${productPrice}`;
  const data = new FormData();
  data.append("string", string);
  const postData = async (url, data) => {
    let res = await fetch(url, {
      method: "POST",
      mode: "no-cors",
      body: data,
    });
    try {
      return await res.text();
    } catch (e) {
      console.log(e);
    }
  };
  const form = document.querySelector("#donate-form");
  try {
    postData(`${path}/assets/services/signature.php`, data).then((res) => {
      merchantSignature = res;
      form.querySelectorAll("input").forEach((input) => {
        console.log(input.name);
        switch (input.name) {
          case "merchantSignature":
            input.value = merchantSignature;
            break;
          case "orderDate":
            input.value = orderDate;
            break;
          case "productName[]":
            input.value = productName;
            break;
          case "currency":
            input.value = currency;
            break;
          case "language":
            input.value = language;
            break;
          case "orderReference":
            input.value = orderReference;
            break;
            case 'returnUrl': 
            input.value += returnUrl;

            break;
         
        }
      });
      form.querySelector("button").click();
    });
  } catch (e) {
    console.log(e);
  }
}
