
import { lenghtCorrect, arrayToChunks } from "./helpers";
import { spinner } from "./elements";

export default function pressAboutPage() {
  const pressAbout = document.querySelector(".press-about__page");

  if (pressAbout) {
    const parent = pressAbout.querySelector("[data-pressAbout]");

    const btn = document.createElement("button");
    btn.classList = "btn btn_outline btn_sm";
    btn.innerHTML = `<span class="fz-14">${
      !isEn ? "Що ще пишуть" : "See more"
    }</span>`;
    btn.dataset.pressbtn = "";

    let articles = arrayToChunks(9, press);
    let index = 1;

    parent.appendChild(spinner);
    setTimeout(() => {
      parent.innerHTML = "";
      renderArticles(articles, 0, parent);
      if (press.length > 9) {
        parent.insertAdjacentElement("afterend", btn);
      }
    }, 750);
    if (btn) {
      btn.addEventListener("click", () => {
        btn.style.opacity = "0";
        btn.insertAdjacentElement("beforebegin", spinner);
        setTimeout(() => {
          spinner.remove();
          btn.style.opacity = "1";
          renderArticles(articles, index, parent);
          index++;
        }, 500);
      });
    }

    function renderArticles(arr, i, parent) {
      if (i < arr.length) {
        const fragment = document.createDocumentFragment();

        arr[i].forEach(({ title, date, link, mainImg }) => {
          const wrapper = document.createElement("div");
          wrapper.classList = "press-about__item";
          let titlePost = lenghtCorrect(title, 60);
          // if (title.length > 59) {
          //   titlePost = titleLenghtCorrect(".news-item__title");
          // }
          wrapper.innerHTML = `
          <div class='image-wrapper'>
        <div class="image">
        <img src="${mainImg}" alt="${titlePost}" class='lazyload'>
        </div>
        </div>
        <h3 class="fz-34 f-600">${titlePost}</h3>
          <div class="date fz-16">${!isEn ? "Опубліковано:" : ""} ${date}</div>
          <a href="${link}" class="t-upper fz-14 f-600">${
            !isEn ? "Переглянути" : "View the material"
          }</a>
      `;

          fragment.appendChild(wrapper);
        });

        parent.appendChild(fragment);
        if (i === arr.length - 1) {
          btn.remove();
        }
      } else {
        return;
      }
    }
  }
}
