
import { arrayToChunks, lenghtCorrect } from "./helpers";
import { spinner } from "./elements";

export default function newsPage() {
  const newsPage = document.querySelector(".news__page");

  if (newsPage) {
    const parent = newsPage.querySelector("[data-news]");

    const btn = document.createElement("button");
    btn.classList = "btn btn_outline btn_sm m-a";
    btn.innerHTML = `<span class="fz-14">${!isEn ? "Переглянути ще новини" : "See more"}</span>`;
    btn.dataset.newsbtn = "";

    let index = 1;
    let newsItems = arrayToChunks(4, news);

    parent.appendChild(spinner);
    setTimeout(() => {
      parent.innerHTML = "";

      renderArticles(newsItems, 0, parent);
      parent.insertAdjacentElement("afterend", btn);
    }, 750);

    btn.addEventListener("click", () => {
      btn.style.opacity = "0";
      btn.insertAdjacentElement("beforebegin", spinner);
      setTimeout(() => {
        spinner.remove();
        btn.style.opacity = "1";
        renderArticles(newsItems, index, parent);
        index++;
      }, 500);

      
    });

    function renderArticles(arr, i, parent) {
      if (i < arr.length) {
        const fragment = document.createDocumentFragment()
        arr[i].forEach(({ title, date, url, descr, mainImg }) => {
          const wrapper = document.createElement("div");
          wrapper.classList = "news-item d-flex";
          let shortDescr = lenghtCorrect(descr, 200);
          wrapper.innerHTML = `
          <div class="news-item__title"> 
            <h3 class="fz-34 f-600">${title}</h3>
            <div class="date fz-16">${!isEn ? "Додано" : "Published"}: ${date}</div>
          </div>
          <div class="news-item__descr">
            <div class="descr fz-18 ${!shortDescr ? 'none' : ''}">${shortDescr}</div>  
            <a href="${url}" class="t-upper fz-14 f-600">${!isEn ? "Переглянути" : "View"}</a> 
            <div class='image-wrapper'>
              <div class="image">
              ${mainImg}
              </div>
            </div>
          </div>
      `;
          fragment.appendChild(wrapper);
          // return fragment;
        });
 
       
        if (i === arr.length - 1) {
          btn.remove();
        }
        return parent.appendChild(fragment);
      } else {
        return;
      }
    }
  }
}
