
import modalsEvents from './modalsEvents'
export default class ChangePhotoSlider {
  constructor(el) {
    this.wrapper = el;
    this.nav = this.wrapper.querySelector('.ads-item-photos__nav')
    this.btns = this.wrapper.querySelectorAll("button");
    this.pagination = this.wrapper.querySelector("[data-pagination]");
    this.previewItems = this.pagination.querySelectorAll(".image");
    this.mainPhoto = this.wrapper.querySelector("[data-mainphoto]");
    this.slideNumber = this.wrapper.querySelector("[data-number]");
  }
  renderActive(i) {
   
    this.slideNumber.innerText = i + 1;

    if(this.previewItems.length == 1){
      this.nav.classList.add('d-none'); 
      this.pagination.classList.add('d-none'); 
      let src = this.previewItems[0].firstElementChild.src;
      this.mainPhoto.firstChild.src = src;
    }
    if (this.slideNumber.innerText == 1) {
      this.btns[0].classList.add("disabled");
      this.previewItems[0].classList.add("active");
    } else{
      this.btns[0].classList.remove("disabled");
    }
    if (this.slideNumber.innerText == this.previewItems.length) {
      removeClass(this.btns, "disabled");
      this.btns[1].classList.add("disabled");
    } else{
      this.btns[1].classList.remove("disabled");
    }
    if (i < this.previewItems.length) {
      let src = this.previewItems[i].firstElementChild.src;

      this.mainPhoto.firstChild.src = src;
      removeClass(this.previewItems, "active");
      this.previewItems[i].classList.add(
        "active"
      );
    } else {
      return;
    }
  }
  addIndexToPrev(){
    this.previewItems.forEach((item, i) => {
      item.dataset.imgItem = i+1;
    });
  }
  imgPopup(){
    this.mainPhoto.addEventListener('click', () => {
      modalsEvents(this.mainPhoto);
    })
  }
  addZoomInfo(){
    const div = document.createElement('div');
    div.classList = 'pos-a zoom-info';
    this.wrapper.classList.add('pos-r');
    div.innerText = `${!isEn ? 'Для збільшення зображення - натисніть на фото' : ''}`;

    this.wrapper.insertAdjacentElement('afterbegin', div)
  }
  init() {

    this.addZoomInfo()
    this.addIndexToPrev();
    this.slideNumber.innerText = 1;
    this.renderActive(0);
    this.imgPopup()
    this.btns.forEach((btn) => {
      if (this.slideNumber.innerText == 1) {
        this.btns[0].classList.add("disabled");
        this.previewItems[0].classList.add("active");
      }
      btn.addEventListener("click", () => {

        if (btn.classList.contains("prev")) {
          if (this.slideNumber.innerText != 1) {
            this.slideNumber.innerText--;
            this.renderActive(+this.slideNumber.innerText - 1)
          }
        } else {
          if (this.slideNumber.innerText < this.previewItems.length) {
            this.slideNumber.innerText++;
            this.renderActive(+this.slideNumber.innerText - 1);
          }
        }
      });
    });
    this.previewItems.forEach((item, i) => {
      item.addEventListener('click', ()=> {
        this.renderActive(i);
      })
    })
  }
}

function removeClass(arr, className) {
  arr.forEach((btn) => {
    btn.classList.remove(className);
  });
}
