export default function paginations(options = {}, arr, render) {
  let defaultIndex = 1,
    itemsOnPage = options.itemsOnPage,
    pageIndex = Math.ceil(arr.length / itemsOnPage);
  let pagiWrapper = document.querySelector(".pagination");

  if (pageIndex > 1) {
    pagination(defaultIndex, pageIndex, render);
  } else {
    render(arr);
  }

  function pagination(index, pageLen = pageIndex, render) {
    pagiWrapper.innerHTML = "";

    let current = index,
      last = pageLen,
      delta = 2,
      left = current - delta,
      right = current + delta + 1,
      range = [],
      rangeWithDots = [],
      l = null;
    for (let i = 1; i <= last; i++) {
      if (i == 1 || i == last || (i >= left && i < right)) {
        range.push(i);
      }
    }

    for (let i of range) {
      if (l) {
        if (i - l === 2) {
          rangeWithDots.push(l + 1);
        } else if (i - l !== 1) {
          rangeWithDots.push("...");
        }
      }
      rangeWithDots.push(i);
      l = i;
    }

    for (let i = 0; i < rangeWithDots.length; i++) {
      let paginationItem = document.createElement("li");

      if (rangeWithDots[i] == current) {
        paginationItem.classList.add("pagination__item", "current");
      } else if (rangeWithDots[i] == "...") {
        paginationItem.addEventListener("click", (e) => e.preventDefault());
        paginationItem.classList.add("pagination__item");
      } else {
        paginationItem.classList.add("pagination__item");
        paginationItem.addEventListener("click", changePageIndex);
      }
      paginationItem.innerHTML = `
          <a >${rangeWithDots[i]}</a>
          `;
      pagiWrapper.append(paginationItem);
    }
    //re-render Post
   
    render(arr, current * itemsOnPage - itemsOnPage, current * itemsOnPage);
 
  }

  function changePageIndex(event) {
    let clickedItem = Number(event.target.innerText);


    pagination(clickedItem, pageIndex, render);
   
  }
}
