import ChangePhotoSlider from "./changePhotoSlider";
import { attachBtnModal, renderProgress } from "./postsRenderHelpFunc";
import {renderBtns, renderStatus, renderBudget, renderStatusIcon} from "./objectsPage";

export default function singleObject() {
  const singleObj = document.querySelector(".single-obj__page");

  if (singleObj) {
    const {status, start, goal, postId, doc} = objects.find(item => item.postId == singleObj.querySelector('.single-obj').dataset.id);


    new ChangePhotoSlider(
      document.querySelector("[data-objectsphotos]")
    ).init();
    document.querySelector('.objects-item-photos__main ').insertAdjacentHTML('afterbegin', renderStatusIcon(status));
document.querySelector('.filter-items__wrapper').insertAdjacentHTML('afterbegin', renderStatus(status));
    document.querySelector('.objects-item__footer').insertAdjacentHTML('afterbegin', renderBudget(status, start, goal));
    document.querySelector('.objects-item__footer').insertAdjacentHTML('beforeend', renderBtns(status, postId, doc));
     attachBtnModal("[data-donate]");
    renderProgress();
    const obj = objects.filter(item => item.status === '3')

    obj.forEach(item => {
      document.querySelector('.swiper-renewed').firstElementChild.appendChild(renderItem(item))


    })
  }
}
 function renderItem(obj){
  let {title, city,   city_en,
    goal, photo} = obj
    let img = photo[0].img
    const fragment = document.createDocumentFragment();

    const div = document.createElement("div");
    div.classList = "swiper-slide";



    div.innerHTML = `            <div class="renewed-item  bg-white bdr-10">
    <div class="renewed-item__img pos-r">
      <div class="image"><img  data-src="${img}" alt="" class="swiper-lazy">
         <div class="swiper-lazy-preloader"></div>
      </div>
       
      <div class="status pos-a status_3">
        <svg class="icon">
          <use xlink:href="#status-3"></use>
        </svg>
      </div>
    </div>
    <div class="renewed-item__descr">
      <h3>
      ${title}
      </h3>
      <div class="city t-secondary fz-18">
      ${city}
      </div>
      <div class="goal fz-24 t-ac f-600">
        Зібрано  ${goal} грн
      </div>
    </div>
  
  </div>`
    
    fragment.appendChild(div);
    return fragment;
 }