import {
  renderFilterLabels,
  createLabelsItem,
  renderNoImg,
  attachBtnModal,
  renderProgress,
} from "./postsRenderHelpFunc";

import { loadItems } from "./elements";
import paginations from "./pagination";

export default function objectsPage() {
  const objectsPage = document.querySelector(".objects__page");

  if (objectsPage) {
    const options = {
      itemsOnPage: 9,
    };
    const parent = document.querySelector("[data-objects]");
    const obj = [...objects];

    parent.insertAdjacentElement("beforebegin", loadItems);

    const renderObj = function (arr, startPos, endPost) {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
      loadItems.classList.remove("d-none");
      loadItems.classList.remove("hide");
      loadItems.classList.add("show");
      parent.classList.add("d-none");
      parent.classList.add("hide");
      parent.innerHTML = ``;
      arr.slice(startPos, endPost).map((post) => {
        parent.appendChild(renderObject(post));
      });

      document.querySelectorAll("[data-objectsPhotos]").forEach((item) => {
        // new ChangePhotoSlider(item).init();
        attachBtnModal("[data-donate]");
        renderProgress();
      });

      setTimeout(() => {
        loadItems.classList.remove("show");
        loadItems.classList.add("hide");
        loadItems.classList.add("d-none");
        parent.classList.remove("d-none");
        parent.classList.remove("hide");
      }, 500);
    };

    paginations(options, obj, renderObj);
  }
}
function renderObject(post) {
  try {
    const fragment = document.createDocumentFragment();
    let {
      postId,
      date,
      city,
      city_en,
        link,
      region,
      region_en,
      descr,
      descr_en,
      doc,
      goal,
      types,
      types_en,
      infrostructure,
      infrostructure_en,
      title,
      title_en,
      start,
      photo,
      status,
    } = post;
    const div = document.createElement("div");
    div.classList = "objects-item";
    status === '3' ? div.classList.add('done') : '';
    div.id = `objects-${postId}`;
    const typesItems = createLabelsItem(types);
    const typesItems_en = createLabelsItem(types_en);
    const infrostructureItems = createLabelsItem(infrostructure);
    const infrostructureItems_en = createLabelsItem(infrostructure_en);
    div.innerHTML = `
<div class="objects-item__photos ads-item-photos" data-objectsPhotos>
<div class="objects-item-photos__main pos-r ">
${renderStatusIcon(status)}
  <div class="image" >${photo ? renderImages(photo, isEn, link, title, title_en) : renderNoImg()}
  </div>
</div>





</div>

<div class="objects-item__header d-flex">
<div>
  ${renderTitle(isEn, title, title_en, link)}
  <div class="location f-600 t-secondary fz-16"> ${
    !isEn ? region + " область" : region_en
  } ,  ${!isEn ? city : city_en}</div>
</div>
<div>
<div class="date f-600 fz-16 t-secondary">${date}</div>
<div class="f-500 fz-14 t-secondary">#${postId}</div>
</div>

</div>
<div class="objects-item__info">

<div class="descr">
${!isEn ? descr : descr_en}
</div>
${renderBudget(status, start, goal)}


</div>
<div class="objects-item__filter pos-r d-flex">
<div class="filter-items__wrapper">
${renderStatus(status)}
<div class="filter-items">
<div class="filter-items__infrastructure">
  <div class="filter-items__title fz-14 f-700">
  ${!isEn ? "Тип інфраструктури:" : "Type of infrastructure:"}
    
  </div>
  <div class="d-flex">
  ${
    !isEn
      ? infrostructureItems
        ? renderFilterLabels(infrostructureItems)
        : "<span class='no-label'>'Не вказано'</span>"
      : infrostructureItems_en
      ? renderFilterLabels(infrostructureItems_en)
      : "<span class='no-label'>Not specified</span>"
  }

  </div>
</div>
</div>
  <div class="filter-items">
    <div class="filter-items__types">
      <div class="filter-items__title fz-14 f-700">
        ${!isEn ? "Види ремонтних робіт:" : "See repair activities:"}
      </div>
      <div class="d-flex">
      ${
        !isEn
          ? typesItems
            ? renderFilterLabels(typesItems)
            : "<span class='no-label'>Не вказано</span>"
          : typesItems_en
          ? renderFilterLabels(typesItems_en)
          : "<span class='no-label'>Not specified</span>"
      }

      </div>
    </div>
  </div>


</div>
${renderBtns(status, postId, doc)}
</div>


`;

    fragment.appendChild(div);
    return fragment;
  } catch (e) {
    console.log(e);
  }
}

function  renderTitle(isEn, title, title_en, link){
  if (isEn){

    return `
    <div class="name f-600 fz-16">${title_en}</div>
    `;


  }
  else{
    return  `  <a href="${link}" target="_blank" class="name f-600 fz-16">${title}</a>`;
  }
}
function renderImages(arr, isEn, link, title, title_en) {
  let wrapper = "";
  arr.forEach((item, i) => {
    if (i <= 0) {
      if (item) {
        if (isEn){
          wrapper += `<div class="image"><img class="lazyload" data-src="${item.img}" alt="${title_en}"/></div>`;
        }
        else {
          wrapper += `<a href="${link}" class="image d-flex" target="_blank"><img class="lazyload" data-src="${item.img}" alt='${isEn ? title_en : title}'/></a>`;
        }
      }
    }
  });

  return wrapper;
}
function renderStatusIcon(status) {
  switch (status) {
    case "1":
      return `<div class='status pos-a status_1'>
        <svg class="icon">
          <use xlink:href="#status-1"></use>
        </svg>
      </div>`;

    case "2":
      return `<div class='status pos-a status_2'>    
        <svg class="icon">
          <use xlink:href="#status-2"></use>
        </svg>
      </div>`;
    case "3":
      return `<div class='status pos-a status_3'>
        <svg class="icon">
          <use xlink:href="#status-3"></use>
        </svg>
      </div>`;
  }
}
function renderStatus(status) {
  if (status !== "3") {
    return `
    <div class="filter-items">
<div class="filter-items__status">
  <div class="filter-items__title fz-14 f-700">
  ${!isEn ? "Статус:" : "Status"}
    
  </div>
  <div class="d-flex">
  <span>
  ${statusName(status)}
  </span>
  

  </div>
</div>
</div>`;
  } else {
    return "";
  }
}

function renderBtns(status, postId, doc) {
  if (status !== "3") {
    return `<div class="objects-item__btns d-flex">
    <button class="btn btn_sm  btn_ac" data-donate='${postId}'>
    ${!isEn ? "Допомогти" : "Donate"}
    </button>
    <a href="${doc}" target='blank' class="btn  btn_sm btn_outline btn_outline-ac"><span class="doc">       <svg class="icon">
    <use xlink:href="#doc"></use>
    </svg></span><span>   ${!isEn ? "ДОКУМЕНТація" : "DOCUMENTATION"}</span></a>
    </div>`;
  } else {
    return `<div class="objects-item__done pos-a">${statusName(status)}</div>`;
  }
}

function renderBudget(status, start, goal) {
  if (status !== "3") {
    return `<div class="budget" data-start="${start}" data-goal="${goal}">
    <h3 class="t-secondary fz-16 f-600">
    ${!isEn ? "Сума інвестицій:" : "Donation amount:"}
    </h3>
    <div class="budget__info d-flex  f-600">
      <div class="start fz-24">
    
      </div>
      <div class="goal t-secondary">
    
      </div>
    
    </div>
    <div class="budget__progress pos-r">
      <span class="pos-a"></span>
    </div>
    </div>`;
  } else {
    return `<div class="budget budget_done">
    <h3 class="t-secondary fz-16 f-600">
    ${!isEn ? "Було зібрано" : "Donation amount"}
    </h3>
    <div class="budget__info d-flex  f-600">
  
      <div class="goal">
    ${!isEn ? goal : "≈" + Math.round(+goal / 36)} ${!isEn ? " грн" : " $"}
      </div>
    
    </div>
 
    </div>`;
  }
}

function statusName(status) {
  switch (status) {
    case "1":
      return !isEn ? "ГОТУЄМОСЯ ДО ВІДНОВЛЕННЯ" : "Preparing for rennovation";

    case "2":
      return !isEn ? "ВІДНОВЛЮЄМО" : "Renewing";
    case "3":
      return !isEn ? "ВІДНОВИЛИ" : "Renovated";
  }
}

export {renderBtns, renderBudget, renderStatusIcon, renderStatus}
