import selected from "./selected";
import { regions, profession } from "./base";

export default class Dropdown {
  constructor(selector) {
    this.dropdown = document.querySelector(selector);
    this.menu = this.dropdown.querySelector('.dropdown__menu');
  }
  removeOpen(arr) {
    arr.forEach((item) => {
      item.classList.remove("open");
    });
  }
  openMenu(el) {

    const allDrops = document.querySelectorAll(".dropdown");
    if (!el.classList.contains("open") && allDrops) {
      this.removeOpen(allDrops);
      el.classList.add("open");
  
    } else {
      el.classList.remove("open");
    }
  }
  selectItem(target, replace, attr) {
    target.classList.add("select");

    let text = replace.innerText;
    let data = replace.getAttribute(`data-${attr}`);
    switch (attr) {
      case "region":
        selected.region = data;
        break;
      case "city":
        selected.city = data;
        break;
      case "category":
        selected.category = data;

        break;
    }
    if (text.length >= 18) {
      target.innerText = `${text.slice(0, 15)}...`;
    } else {
      target.innerText = text;
    }

    target.setAttribute(`data-${attr}`, data);
  }
  init(attr) {
    if (this.dropdown) {
 
      const select = this.dropdown.querySelector(".dropdown__selected");

      if (attr === "profession") {
        this.renderProfessions();
      }
      if (attr === "region") {
        this.renderRegions();
      }
      this.dropdown.addEventListener("click", (e) => {
        if (!e.target.classList.contains("dropdown__menu")) {
          this.openMenu(this.dropdown);
          this.renderCities();
        }
      });
      this.menu.addEventListener("click", (e) => {
      
        if (e.target.classList.contains("dropdown__item")) {
          this.selectItem(select, e.target, attr);
        }
      });
    } else {
      return;
    }
  }
  renderCategories() {
    const categoryDropdown = document.querySelector(
      '[data-category="dropdown"]'
    );
    if (categoryDropdown) {

      const fragment = document.createDocumentFragment();
      this.menu.appendChild(
        this.renderItems(categories, fragment, "category")
      );
    }
  }
  renderCities() {
    const regionDropdown = document.querySelector('[data-city="dropdown"]');
    if (regionDropdown) {
      const fragment = document.createDocumentFragment();
      if (selected.region === "") {
        this.menu.innerHTML = "";
        const li = document.createElement("li");
        li.classList.add("dropdown__item");
        li.innerText = "Выбирете регион...";
        li.dataset.city = "city";
        this.menu.appendChild(li);
      } else {
        this.menu.innerHTML = "";
        const region = regionsList.find((item) => {
          let findItem = {};
          for (let key in item) {
            if (key.toLowerCase() == selected.region) {
              findItem = { ...item.cities };
              return findItem;
            }
            return;
          }
          return findItem;
        });
        this.menu.appendChild(this.renderLi("city"));
        this.menu.appendChild(
          this.renderItems(region.cities, fragment, "city")
        );
      }
    }
  }
  renderRegions() {

    // if (this.dropdown) {
      
      const fragment = document.createDocumentFragment();

      this.menu.appendChild(
        this.renderItems(regions, fragment, "region")
      );
    // }
  }
  renderProfessions() {
    const regionDropdown = document.querySelector(
      '[data-profession="dropdown"]'
    );
    if (regionDropdown) {
      const dropdownWrapper = regionDropdown
        .querySelector(".dropdown-wrapper")
        .querySelector(".row");

      const fragment = document.createDocumentFragment();
      // dropdownWrapper.appendChild(this.renderLi("region"));
      dropdownWrapper.appendChild(
        this.renderItems(profession, fragment, "profession")
      );
    }
  }
  renderItems(arr, fragment, attr) {
    arr.forEach((item) => {
      const li = document.createElement("li");
      li.classList = "dropdown__item";
      li.innerText = item;
      li.dataset[`${attr}`] = item;
      fragment.appendChild(li);
    });
    return fragment;
  }
  renderLi(attr) {
    const li = document.createElement("li");
    li.classList = "dropdown__item not-active";
    switch (attr) {
      case "region":
        li.innerText = "Батальйон...";
        break;
      case "city":
        li.innerText = "Город...";
        break;
    }
    li.dataset[attr] = attr;
    return li;
  }
}
