import IMask from "imask";
import selected from "./selected";
import modalsEvents from "./modalsEvents";
const content = {
  field: {
    en: "This field is required",
    ua: "Поле обов'язкове для заповнення",
  },
  template: {
    en: "Fill in according to the template",
    ua: "Заповніть відповідно до шаблону",
  },
  send: {
    en: "Donate",
    ua: "Відправити",
  },
  name: {
    en: "Name:",
    ua: `Введіть ім'я`,
  },

  thankTitle: {
    en: "THANK YOU VERY MUCH FOR YOUR HELP!",
    ua: "ЩИРО ДЯКУЄМО ЗА ДОПОМОГУ!",
  },
  thankDescr: {
    en: "You can read our reports below on the website",
    ua: "Ви можете ознайомитися із нашими звітами нижче на сайті",
  },
};

let validRegex =
  /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

export default class Form {
  constructor(form) {
    this.form = document.querySelector(form);
    this.inputs = this.form.querySelectorAll("input");
    this.btn = this.form.querySelector('button[type="submit"]');
    this.btnText = this.btn.innerText;
    this.types = this.form.querySelectorAll('[name="checkbox-types"]');
    this.upload = this.form.querySelector('[type="file"]')
      ? this.form.querySelector('[type="file"]')
      : false;
    this.professions = this.form.querySelectorAll(
      '[name="checkbox-profession"]'
    );
    this.materials = this.form.querySelectorAll('[name="checkbox-materials"]');
    this.txtArea = this.form.querySelector("textarea");
    this.isRu = false;
    this.message = {
      failure: {
        ru: "Что-то пошло не так...",
        ua: "Щось пішло не так...",
      },
    };
    // this.path = `assets/services/telegramBot.php`;
    this.path = `${path}/assets/services/telegramBot.php`;
    this.formData = {
      title: "Надати матеріал",
      phone: "",
      name: "",
      email: "",
      region: "",
      city: "",
      info: "",
      types: "",
      professions: "",
      materials: "",
    };
    this.files = [];
  }
  uploadFiles() {
    if (this.upload) {
      const open = this.form.querySelector("[data-upload]");
      const container = this.form.querySelector("[data-container]");

      open.addEventListener("click", () => {
        if (this.files.length >= 5) {
          container
            .closest(".form__upload")
            .querySelector(".error").style.display = "block";
          open.classList.add("disabled");

          return;
        }
        this.upload.click();
      });

      this.upload.addEventListener("change", () => {
        if (!this.upload.files.length || this.upload.files.length > 5) {
          return;
        }

        if (this.upload.files.length + this.files.length > 5) {
          let end = 5 - this.files.length;
          let arr = [...this.upload.files].slice(0, end);
          this.files = [...arr, ...this.files];
        } else {
          this.files = [...this.upload.files, ...this.files];
        }
        container.innerHTML = ``;
        this.files.forEach(({ name = file[0].name }) => {
          container.insertAdjacentHTML(
            "afterbegin",
            `
          <div class="form__upload-item d-flex t-secondary">
          <div class="pic d-flex"> <svg class="icon">
            <use xlink:href="#pic"></use>
          </svg></div>
          <div class="name fz-14 "><span>${name}</span></div>
          <div class="delete" data-name="${name}">&times;</div>
        </div>
            `
          );
        });
      });
      container.addEventListener("click", (event) => {
        if (!event.target.dataset.name) {
          return;
        }
        const { name } = event.target.dataset;
        this.files = [...this.files.filter((file) => file.name !== name)];
  
        if (!this.files.length) {
          this.upload.style.display = "none";
        }

        const block = container
          .querySelector(`[data-name="${name}"]`)
          .closest(".form__upload-item");

        block.classList.add("removing");
        setTimeout(() => block.remove(), 300);
        if (this.files.length < 5) {
          container
            .closest(".form__upload")
            .querySelector(".error").style.display = "none";
          open.classList.remove("disabled");

          return;
        }
      });
    } else {
      return;
    }
  }

  validateEmptyField() {
    let { field } = content;
    let isValid = true;
    let validator = {
      name: null,
      tel: null,
      email: null,
    };

    this.inputs.forEach((input) => {
      if (input.name === "name") {
        if (!input.value.trim()) {
          input.parentNode.classList.add("invalid");
          input.nextElementSibling.innerHTML = `${
            !this.isRu ? field.ua : field.ru
          }`;
          return (isValid = false);
        } else {
          this.valid(input);
          input.nextElementSibling.innerText = "";

          validator.name = true;
          return (isValid = true);
        }
      } else if (input.name === "tel") {
        if (input.value.indexOf("_") === -1) {
          this.valid(input);
          input.nextElementSibling.innerText = "";

          validator.tel = true;
          return (isValid = true);
        } else {
          input.parentNode.classList.add("invalid");
          input.nextElementSibling.innerHTML = `${
            !this.isRu ? field.ua : field.en
          }`;

          return (isValid = false);
        }
      } else if (input.name === "email") {
        if (!input.value.match(validRegex)) {
          input.parentNode.classList.add("invalid");
          input.nextElementSibling.innerHTML = `${
            !this.isRu ? field.ua : field.ru
          }`;
          return (isValid = false);
        } else {
          this.valid(input);
          input.nextElementSibling.innerText = "";

          validator.email = true;
          return (isValid = true);
        }
      }
    });

    if (validator.name && validator.tel && validator.email) {
      return isValid;
    }
  }
  valid(input) {
    input.parentNode.classList.add("valid");
    input.parentNode.classList.remove("invalid");
  }
  invalid(input) {
    input.parentNode.classList.add("invalid");
    input.parentNode.classList.remove("valid");
  }
  createMask(input) {
    let maskOptions = {
      mask: "+38 (000) 000 - 00 - 00",
      lazy: false,
    };
    let mask = new IMask(input, maskOptions);
  }

  checkInputs() {
    this.inputs.forEach((input) => {
      if (input.name === "name") {
        this.checkNameInput(input);
      } else if (input.name === "tel") {
        this.checkPhoneInput(input);
      } else if (input.name === "email") {
        this.checkEmailInput(input);
      } else if (input.name === "city") {
        input.addEventListener("input", () => {
          this.formData.city = input.value;
        });
      } else {
        return;
      }
    });
    this.txtArea.addEventListener("input", () => {
      this.formData.info = this.txtArea.value;
    });
  }

  checkNameInput(input) {
    let { name } = content;
    let isValid = false;
    input.addEventListener("keypress", function (e) {
      if (!e.key.match(/^[A-Za-zА-Яа-яЁё\s]/)) {
        e.preventDefault();
      }
    });
    input.addEventListener("input", () => {
      if (input.value.length >= 2) {
        this.valid(input);
        this.formData.name = input.value.trim();
        isValid = true;
      } else {
        this.invalid(input);
        isValid = false;
      }
      if (!isValid) {
        input.nextElementSibling.innerHTML = `${
          !this.isRu ? name.ua : name.en
        }`;
      } else {
        input.nextElementSibling.innerText = "";
      }
    });
  }
  checkEmailInput(input) {
    let { email } = content;
    let isValid = false;

    input.addEventListener("input", () => {
      if (input.value.match(validRegex)) {
        this.valid(input);
        this.formData.email = input.value;
        isValid = true;
      } else {
        this.invalid(input);
        isValid = false;
      }
      if (!isValid) {
        input.nextElementSibling.innerHTML = `${
          !this.isRu ? email.ua : email.en
        }`;
      } else {
        input.nextElementSibling.innerText = "";
      }
    });
  }

  checkPhoneInput(input) {
    let { template } = content;
    let isValid = false;
    this.createMask(input);
    input.addEventListener("input", () => {
      if (input.value.indexOf("_") === -1) {
        this.valid(input);
        this.formData.phone = input.value;
        isValid = true;
      } else {
        this.invalid(input);
        isValid = false;
      }
      if (!isValid) {
        input.nextElementSibling.innerHTML = `${
          !this.isRu ? template.ua : template.en
        }`;
      } else {
        input.nextElementSibling.innerText = "";
      }
    });
  }
  resetForm(btn) {
    btn.classList.remove("disabled");
    this.formData = {
      title: this.form.dataset.title,
      phone: "",
      name: "",
      email: "",
      region: "",
      city: "",
      info: "",
      types: "",
      professions: "",
      materials: "",
    };
    this.file = [];
    btn.innerHTML = this.btnText;
    if (btn.dataset.send) {
      btn.removeAttribute("data-send");
    }
    if (btn.dataset.error) {
      btn.removeAttribute("data-error");
    }
    this.form.querySelector("[data-container]").innerHTML = "";
    this.form.reset();
    this.checkInputs();
  }

  async postData(url, data, btn) {
    try {
      const response = await fetch(url, {
        method: "POST",
        body: JSON.stringify(data),
        headers: {
          "Content-Type": "application/json",
        },
      });
      modalsEvents(btn);
      this.resetForm(btn);
    } catch (error) {
      btn.error = true;

      modalsEvents(btn);
      this.resetForm(btn);
      console.error("Ошибка:", error);
    }
  }

  async createAd(data) {
    try {
      const response =  await fetch(ajax_url, {
        method: "POST",
        body: data,
      });
      return response;
    } catch (e) {
      console.log(e);
    }
  }


  addCheckboxesValue(arr, name) {
    this.formData[name] = [];
    arr.forEach((input) => {
      if (input.checked) {
        this.formData[name] = [input.value, ...this.formData[name]];
      }
    });
    if (this.formData[name].length) {
      this.formData[name] = this.formData[name].join(", ");
    } else {
      this.formData[name] = "";
    }
  }

  init() {
    this.checkInputs();
    this.uploadFiles();
    this.form.addEventListener("submit", (e) => {
      e.preventDefault();

      if (this.validateEmptyField()) {
        e.submitter.dataset.send = true;
        if (
          this.types.length &&
          this.professions.length &&
          this.materials.length
        ) {
          this.addCheckboxesValue(this.types, "types");
          this.addCheckboxesValue(this.professions, "professions");
          this.addCheckboxesValue(this.materials, "materials");
        }

        this.formData.title = this.form.dataset.title;
        this.formData.region = selected.region;
        this.formData.types = selected.region;
        if (this.form.dataset.title === "Запросити допомогу") {
          const data = new FormData();
          for (let key in this.formData) {
            data.append(`${key}`, `${this.formData[key]}`);
          }
          for (let i = 0; i < this.files.length; i++) {
            data.append(`file_${i + 1}`, this.files[i]);
          }
          data.append(`action`, "create_ads");

           this.createAd(data);
   
        }
        e.submitter.classList.add("disabled");
        e.submitter.innerHTML = `<span class="spinner-wrapper"><svg class="icon">
        <use xlink:href="#spinner-ac"></use>
      </svg></span>`;

        this.postData(this.path, this.formData, e.submitter);
      }
    });
  }
}
