export default function modalsEvents(target) {
  const { titleCallback, titleSign, name, model, send } = content;

  // const modalContent = document.createElement("div");
  if (target.hasAttribute("data-number")) {
    renderAdsItemModal(target);
  }
  if (target.hasAttribute("data-donate")) {
    renderDonateModal(target);
    document.querySelectorAll("[data-label").forEach((btn) => {
      btn.addEventListener("click", function () {
        gtag("event", "click", {
          event_category: "donate_objects",
          event_label: `${this.dataset.label}`,
        });
        createDonate(this);
      });
    });
  }
  if (target.hasAttribute("data-send")) {
    renderThankModal();
  }
  if (target.hasAttribute("data-mainphoto")) {
    renderImgModal(target);
  }

  if (target.hasAttribute("data-error")) {
    renderError();
  }
  new Modal(".modal").openModal();
}

function renderImgModal(target) {
  modal.classList.add("modal-img");
  let src = target.firstChild.src;
  modalBody.innerHTML = `
  <div class="img-modal">
    <img src="${src}" alt="">
  </div>
`;
}

