import { header } from "./elements";
import {getElement} from "./helpers";
const adaptiveElements = () => {
  getElement('#menu-btn')?.addEventListener("click",  () => {
    header.classList.toggle("active");
  });
  if (matchMedia("(max-width: 992px)").matches) {
    if (document.querySelector(".objects")) {
      document.querySelectorAll(".objects-item.done").forEach((item) => {
        item
          .querySelector(".objects-item__done")
          .insertAdjacentElement("beforebegin", item.querySelector(".budget"));
      });
    }
  }
  if (matchMedia("(max-width: 600px)").matches) {
    const logo = header.querySelector(".logo");
    logo.innerHTML = `
    <svg class="icon">
    <use xlink:href="#logo-mobile"></use>
    </svg>`;
  }
};
export default adaptiveElements;
