import modalsEvents from "./modalsEvents";
function renderFilterLabels(arr) {
  if (arr.length) {
    let t = "";
    arr.forEach((item) => {
      t += `<span>${item}</span>`;
      return t;
    });
    return t;
  }
}
function renderLabel(arg) {
  return `<span>${arg}</span>`;
}

function createLabelsItem(arr) {
  if (arr && arr !== "") {
    return arr.split(",");
  }
  return;
}

function renderImages(arr) {
  let wrapper = "";
  arr.forEach((item) => {
    if (item) {
      if (item.img) {
        wrapper += `<div class="image"><img loading="lazy" src="${item.img}" alt='Фото'/>"alt=""></div>`;
        // wrapper += `<div class="image"><img src="${item.img}" alt='Фото'/>"alt=""></div>`;
      } else {
        wrapper += `<div class="image"><img  src="${item}" alt='Фото'/>"alt=""></div>`;
      }
    }
  });

  return wrapper;
}

function renderNoImg() {
  return ` <div class="image">
<img  src="${path}/assets/img/no-image.jpg" alt='Фото'/>"
    alt=""></div>`;
}

function attachBtnModal(selector) {
  const mBtns = document.querySelectorAll(selector);
  mBtns.forEach((item) => {
    item.addEventListener("click", () => {
      modalsEvents(item);
    });
  });
}
function renderProgress() {
  if (document.querySelectorAll(".budget").length > 0) {
    document.querySelectorAll(".budget").forEach((item) => {
      if (item.dataset.start && item.dataset.goal) {
        let start = +item.dataset.start;
        let goal = item.dataset.goal;

        item.querySelector(".start").innerText =
          `${!isEn ? start : "≈" + Math.round(+start / 36)}` +
          `${!isEn ? " грн" : " $"}`;
        item.querySelector(".goal").innerText =
          `${!isEn ? goal : "≈" + Math.round(+goal / 36)}` +
          `${!isEn ? " грн" : " $"}`;
        let progress = Math.ceil((start * 100) / goal);
        item.querySelector("span").style.width = `${progress}%`;
      }
    });
  }
}

export {
  renderFilterLabels,
  renderLabel,
  createLabelsItem,
  renderImages,
  renderNoImg,
  attachBtnModal,
  renderProgress,
};
