export default function renderCheckboxFilters(selector, data, name) {
  const parent = document.querySelector(selector);
  const fragment = document.createDocumentFragment();
  data.forEach((item) => {
    const div = document.createElement("div");
    div.classList = "form__checkbox";
    if(selector === '.materials'){
      div.innerHTML = `
  
      <label class="d-flex">
        <input type="checkbox" name="checkbox-${name}" value="${item}">
        <span class="check">

        </span>
        <span class="text-item fz-14">${item}</span>
      </label>
    `;
    } else{
          div.innerHTML = `
  
    <label class="d-flex">
      <input type="checkbox" name="checkbox-${name}" value="${item}">
      <span class="check">
      <svg class="icon">
      <use xlink:href="#check"></use>
    </svg>
      </span>
      <span class="text-item fz-14">${item}</span>
    </label>
  `;
    }

    fragment.appendChild(div);
  });
  return parent.appendChild(fragment);
}
