import { header } from "./elements";
let loc = location.origin;
const scrollToAnchor = function () {
  const links = document.querySelectorAll(".scrolTo");

  links.forEach((link) => {
    link.addEventListener("click", function (event) {
      event.preventDefault();
      const blockID = link.getAttribute("href");
      const headerHeaight = header.offsetHeight;
      if (
        header.classList.contains("active")
      ) {
        header.classList.toggle("active");
        // headerNav.classList.toggle("active");
        // document.body.classList.toggle("active");
      }

      

      scrollToAnotherPage(blockID, "#about");

      scrollToAnotherPage(blockID, "#form");
      scrollToAnotherPage(blockID, "#donate");

      window.scrollTo({
        top: document.querySelector(`${blockID}`).offsetTop - headerHeaight,
        behavior: "smooth",
      });
    });
  });
};

function scrollToAnotherPage(el, target) {
  if (target) {
    if (el === target) {
      location.href = isEn ?  `${loc}/en/${target}` :`${loc}${target}`;
    }
  } else{
    return;
  }
}
export default scrollToAnchor;
