"use strict";
import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';
import adaptiveElements from "./modules/adaptiveElements";
import scrollToAnchor from "./modules/scrollToAnchor";
import gallerySlider from "./modules/gallerySlider";
import {gtagBtns} from "./modules/elements";

import checkbox from "./modules/checkbox";
import tabs from "./modules/tabs";
import Form from "./modules/Forms";
import adsPage from "./modules/adsPage";
import Dropdown from "./modules/Dropdown";
import pressAboutPage from "./modules/pressAboutPage";
import newsPage from "./modules/newsPage";
import singleNews from "./modules/singleNews";
import singleObject from "./modules/singleObjectPage";
import objectsPage from "./modules/objectsPage";
import {modalBody, header, modalWrapper} from "./modules/elements";
import Modal from "./modules/popups";
import thankPage from "./modules/thankPage";
import {getElement} from "./modules/helpers";
import donatePage from "./modules/donatePage";
import academy from "./modules/academy";

window.addEventListener("DOMContentLoaded", () => {
    try {
        console.log('work')
        scrollToAnchor();
        checkbox();
        if (document.querySelector(".main__page")) {
            tabs();
            if (!isEn) {
                document.querySelector("[data-help]").addEventListener("click", () => {
                    document.querySelector('[data-target="#materials"]').click();
                });

                const attention = document
                    .querySelector(".form__attention")
                    .querySelector(".descr");
                const attentionTogglers = attention.querySelectorAll('[data-attention]');
                attentionTogglers.forEach((toggle) => {
                    toggle.addEventListener("click", function () {
                        attention.classList.toggle("open");
                    });
                });
                new Dropdown(".region-materials").init("region");
                new Dropdown(".region-help").init("region");
                new Form(".form-help").init();
                new Form(".form-materials").init();
            }
            if (gtagBtns) {
                gtagBtns.forEach((btn) => {
                    btn.addEventListener("click", function () {
                        gtag("event", "click", {
                            event_category: "donate",
                            event_label: `${this.dataset.label}`,
                        });
                    });
                });
            }
        }
        getElement('[data-gtag]', true).forEach(gtagBtn => {
            gtagBtn.addEventListener('click', (e) => {
                e.preventDefault();
                gtag('event', gtagBtn.dataset.gtag);
                location.href = gtagBtn.href
            })
        })
        headerFixed();
        getElement('#menu-btn')?.addEventListener('click', function () {
            this.classList.toggle('active');
        })
        adaptiveElements();
        objectsPage();
        pressAboutPage();
        newsPage();
        singleNews();
        singleObject();
        adsPage();
        donatePage();
        thankPage();
        gallerySlider();
        window.addEventListener("scroll", () => {
            headerFixed();
        });
        if (getElement('.academy')) {
            academy();
        }

        function headerFixed() {
            if (matchMedia("(max-width: 600px)").matches) {
                if (window.pageYOffset > 40) {
                    header.classList.add("header_fixed");
                } else {
                    header.classList.remove("header_fixed");
                }
            } else {
                if (window.pageYOffset > 80) {
                    header.classList.add("header_fixed");
                } else {
                    header.classList.remove("header_fixed");
                }
            }
        }

        async function send(data) {
            try {
                const result = await fetch(
                    "https://script.google.com/macros/s/AKfycbx77gWMlEZXErhxsnVGtkFKxQ7SIsRA9AO9udomKapmott1FL2JjZpg0fYLOu_WhNZ_/exec",
                    {
                        method: "POST",
                        body: data,
                    }
                );

                window.location.href = "/thank-you/";
            } catch (e) {
                console.log(e);
            }
        }

        const getUtmParameter = (url, data) => {
            if (url) {
                const urlValues = url.substr(1).split("&");

                const values = urlValues.map((value) => value.split("="));
                values.forEach((item) => {
                    const regex = /utm_/i;
                    let name = item[0].replace(regex, "");
                    data.append(`${name}`, `${item[1]}`);
                });
                return true;
            }
            return false;
        };
    } catch (e) {
        console.log(e);
    }

    async function createAd(data) {
        try {
            await fetch(ajax_url, {
                method: "POST",
                body: data,
            });
        } catch (e) {
            console.log(e);
        }
    }

    if (document.querySelector(".news-page")) {
        document.querySelectorAll(".news-item__title").forEach((item) => {
            if (item.innerText.length > 50) {
                item.innerText = item.innerText.substr(0, 50) + `...`;
            }
        });
    }

    if (document.querySelector(".advertisement")) {
        const mBtns = document.querySelectorAll("[data-modals]");
        mBtns.forEach((item) => {
            item.addEventListener("click", () => {
                const modal = document.querySelector(".modal");

                if (item.dataset.modals === "form") {
                    new Modal(".modal-form").openModal();
                    return;
                }
                const modalBody = modal.querySelector(".modal__body");
                modalBody.innerHTML = `
        <div class="ads-item__modal">
        <div class="wrapper fz-18">
    <h4><strong class="d-block fz-24"> Увага! </strong> Пам’ятайте про заходи безпеки: </h4>
    <ul>
            <li>
              Всі оголошення на сайті - безкоштовні. Якщо від вас вимагають гроші на карту - це шахраї.
            </li>
     
          </ul>
        </div>
          
        
        
        <div class="number"> Номер телефону власника: <strong>${item.dataset.modals}</strong></div>
  
        <a href="tel:${item.dataset.modals}" class="btn fz-18">
          <svg data-v-72457066="" viewBox="0 0 16 16" width="1em" height="1em" focusable="false" role="img" aria-label="telephone fill" xmlns="http://www.w3.org/2000/svg" fill="currentColor" class="bi-telephone-fill b-icon bi">
            <g data-v-72457066="">
              <path fill-rule="evenodd" d="M1.885.511a1.745 1.745 0 0 1 2.61.163L6.29 2.98c.329.423.445.974.315 1.494l-.547 2.19a.678.678 0 0 0 .178.643l2.457 2.457a.678.678 0 0 0 .644.178l2.189-.547a1.745 1.745 0 0 1 1.494.315l2.306 1.794c.829.645.905 1.87.163 2.611l-1.034 1.034c-.74.74-1.846 1.065-2.877.702a18.634 18.634 0 0 1-7.01-4.42 18.634 18.634 0 0 1-4.42-7.009c-.362-1.03-.037-2.137.703-2.877L1.885.511z"></path>
            </g>
          </svg>
          Зв’язатись з власником</a>
        <button type="button" class="btn  btn_outline modal__close fz-18"> Закрити </button>
  
      </div>
      `;
                new Modal(".modal-number").openModal();
            });
        });
    }
    window.addEventListener(
        "orientationchange",
        function () {
            if (screen.height < modalBody.offsetHeight) {
                modalWrapper.style.height = "100%";
                modalBody.style.height = "100%";
                modalBody.style.overflow = "scroll";
            } else {
                modalWrapper.style.height = "auto";
                modalBody.style.height = "auto";
                modalBody.style.overflow = "auto";
            }
        },
        false
    );
});
